      border-bottom: 1px solid #eee;
      font-weight: bold;
      padding: 10px 5px;
      white-space: nowrap;
}
table thead tr th:first-child {
        padding-left: 25px;
}
table thead tr th .arrow {
        opacity: 0;
}
table thead tr th .arrow:before {
          content: '';
          font-size: 18px;
}
table thead tr th .arrow.asc:before {
          content: '↑';
}
