      -webkit-box-align: start;
          -ms-flex-align: start;
              align-items: flex-start;
}
table tbody tr td .delete .icon {
        margin-right: 5px;
}
table tbody tr td .vertical-line {
      width: 4px;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      background-color: #4c73fc;
      opacity: 0;
}
table tbody tr td:first-child {
      padding-left: 25px;
}
table tbody tr:hover {
    background-color: #edf1fe;
    cursor: pointer;
}
table tbody tr:hover td .delete {
      opacity: 1;
      pointer-events: auto;
}
